<template>
  <div class="login-wrap">
    <div class="login-view">
      <h2>系统登录</h2>
      <el-form>
        <el-form-item>
          <el-input v-model="userInfo.username"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model="userInfo.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login()">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userInfo: {
        username: "123456",
        password: "admin",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    login() {
      this.$refs.loginFrom.validate((valid) => {
        if (valid) {
          const { username, password } = this.userInfo;
          setTimeout(function () {
            if (username === "123456" && password === "admin") {
              console.log("登录成功");
              // 登陆成功后，要记录登录的信息，这个信息要所有页面都能获取到，最好不是发请求获取
              // 就需要我们前端记录这个登录信息
              // 我们使用浏览器的本地存储功能  localStorage  sessionstorage
              // 两者的用法完全一样  区别是localStorage一直会存储在浏览器中，除非删除了
              // sessionstorage 浏览器关闭了，就消失了，只存储在了当前的浏览器窗口

              // 存  之能存布尔值  字符串  数字，想要存对象，需要使用JSON.stringfy转成JSON串再存
              // 存了之后，任何地方都可以取
              localStorage.setItem("isLogin", true);

              // 取  取出来的是字符串
              //   localStorage.getItem("isLogin");

              // 删
              // 删一个
              //   localStorage.removeItem("isLogin");
              // 删所有
              //   localStorage.clear();

              // 编程式导航  不需要 router-link 跳转
              this.$router.push("/");
            } else {
              console.log("登录失败");
            }
          }, 300);
        }
      });
    },
  },
};
</script>

<style scoped>
.login-wrap {
  width: 100vw;
  height: 100vh;
  background-color: #2d3a4b;
  padding-top: 250px;
}
.login-view {
  width: 400px;
  margin: 0 auto;
}
.login-view h2 {
  color: #fff;
  text-align: center;
  line-height: 100px;
}
.el-button {
  width: 100%;
}
</style>