// 创建路由
import Vue from "vue";
import VueRouter from "vue-router";
// 导入
import LoginView from '../views/LoginView.vue'


// 1.将路由制作成vue全局的
Vue.use(VueRouter)

// 2.创建路由对象
// routes的匹配规则，从上到下，匹配到一个，之后的就不会匹配了
const routes = [
    {
        // 登录页
        path: '/login',
        name: 'login',
        component: LoginView
    },
    {
        // 首页
        path: '/',
        component: () => import('../views/MainView.vue'),
        children: [
            // 默认子路由
            {
                path: '',
                component: () => import('../views/HomeView.vue'),
                children: [
                    // 默认子路由
                    {
                        path: '',
                        name: 'home',
                        component: () => import('../views/PostListViewOther.vue'),
                    },
                    // 动态子路由
                    {
                        path: 'list/:tab',
                        component: () => import('../views/PostListViewOther.vue'),
                        // 传递动态路由参数，拿到tab的值
                        props: true
                    }
                ]
            },
            {
                path: 'post/:postId',
                name: 'post',
                component: () => import('../views/PostView.vue'),
            }
        ]
    },
    {
        // 404页面
        path: '*',
        name: 'notfound',
        component: () => import('../views/NotFound.vue'),
    },
]

const router = new VueRouter({
    // 路由模式：history或者hash
    mode: 'hash',
    routes
})

// 创建好路由之后可以设置全局守卫了
// 全局前置守卫，只要是路由跳转那么全局前置守卫就会在跳转之前触发

// 守卫函数内 最后必须使用next放行
// 直接next是直接放行
// next('地址')放行到别的地址
const isLogin = true
router.beforeEach((to, from, next) => {
    // to 是要跳转到哪
    // from 是从哪里来
    // next 是放行的方法
    // || 或者
    if (to.path === '/login' || isLogin) {
        next()
    } else {
        next('/login')
    }
})

export default router